import axios from 'axios';
import React, { Component } from 'react';

import ReactPlayer from 'react-player';
import style from './style.module.scss';
import Image from 'react-bootstrap/Image';
import InstagramCarousel from './carousel';
import { Container, Row, Col } from 'reactstrap';

interface InstagramProps {

}

interface InstagramState {
    images: any[];
    carouselItems: any[];
}

export default class Instagram extends Component<InstagramProps, InstagramState> {

    constructor(props: any) {
        super(props);

        this.state = {
            images: [],
            carouselItems: []
        };
    }

    componentDidMount() {

        let token = '9470418333.1c10622.e96698663d564a3d885b7d66efa717c5';
        let num_photos = 10;

        axios.get('https://api.instagram.com/v1/users/self/media/recent/?access_token=' + token + '&count=' + num_photos)
            .then(res => {
                // console.log(res);

                this.setState({
                    images: res.data.data
                });

                this.state.images.forEach(image => {

                    if (image.type === 'carousel') {

                        this.setState({
                            carouselItems: image.carousel_media
                        });
                    }
                });

            })
            .catch(err => {
                console.log(err)
            })

    }

    renderImageType(image: any): JSX.Element {

        switch (image.type) {

            case 'video':
                return (
                    <Col md={8} lg={6} mb={1} key={image.id} className={[style.feed, style.instaLinks].join(' ')}>

                        <ReactPlayer
                            url={image.videos.standard_resolution.url}
                            playing={true}
                            loop={true}
                            light={image.images.standard_resolution.url}
                            height={535}
                            width={535}
                            volume={1}
                            controls={true}
                        />

                        <p>
                            {image.caption.text}
                        </p>
                        <p>
                            <a
                                href={image.link}>{image.likes.count} likes </a> | <a href={image.link}>{image.comments.count} comentários
                            </a>
                        </p>
                    </Col>
                );

            case 'carousel':
                return (
                    <Col md={8} lg={6} mb={1} key={image.id} className={[style.feed, style.instaLinks].join(' ')}>

                        <InstagramCarousel
                            carouselItems={this.state.carouselItems}
                            altText={image.caption.text}
                        />
                        <p>{image.caption.text}</p>
                        <p>
                            <a
                                href={image.link}>{image.likes.count} likes </a> | <a href={image.link}>{image.comments.count} comentários
                            </a>
                        </p>
                    </Col>
                );

            case 'image':
            default:
                return (
                    <Col md={8} lg={6} mb={1} key={image.id} className={[style.feed, style.instaLinks].join(' ')}>

                        <a href={image.link}>
                            <Image alt={image.caption.text} src={image.images.standard_resolution.url} fluid />
                        </a>

                        <p>{image.caption.text}</p>
                        <p>
                            <a
                                href={image.link}>{image.likes.count} likes </a> | <a href={image.link}>{image.comments.count} comentários
                            </a>
                        </p>
                    </Col>
                );
        }
    }

    render() {

        const {
            images
        }: InstagramState = this.state;

        return (
            <Container className={["instafeed", style.instagram].join(' ')}>
                <Row>
                    {images.map((image) => {
                        return this.renderImageType(image);
                    })}

                </Row>
            </Container>
        )
    }

    // render() {

    //     return (
    //         <div className={[style.instagram].join(' ')}>
    //             <embed src="https://web.stagram.com/megalojaborjareis" className={[style.feed].join(' ')}>
    //             </embed>

    //             <iframe
    //                 src="https://www.facebook.com/plugins/page.php?href=https%3A%2F%2Fwww.facebook.com%2Fmegaloja.pt%2F&tabs=timeline&width=1000&height=700&small_header=true&adapt_container_width=true&hide_cover=false&show_facepile=true&appId"
    //                 width="1000" height="700"
    //                 scrolling="no"
    //                 allow="encrypted-media"
    //                 title='Facebook Feed'
    //                 className={[style.facebookFeed].join(' ')}>
    //             </iframe>
    //         </div>
    //     );
    // }
}