import React, { Component } from 'react';
import Image from 'react-bootstrap/Image';
import { Carousel, CarouselItem, CarouselControl, CarouselIndicators } from 'reactstrap';

interface InstagramCarouselProps {
    carouselItems: any[];
    altText: string;
}

interface InstagramCarouselState {
    activeIndex: any;
}

export default class InstagramCarousel extends Component
    <InstagramCarouselProps, InstagramCarouselState> {

    constructor(props: InstagramCarouselProps) {
        super(props);
        this.state = {
            activeIndex: 0
        };
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.goToIndex = this.goToIndex.bind(this);
        this.onExiting = this.onExiting.bind(this);
        this.onExited = this.onExited.bind(this);
    }

    animating: boolean = false;

    onExiting() {
        this.animating = true;
    }

    onExited() {
        this.animating = false;
    }

    next() {

        if (this.animating) return;

        const nextIndex = this.state.activeIndex === this.props.carouselItems.length - 1
            ? 0
            :
            this.state.activeIndex + 1;

        this.setState({
            activeIndex: nextIndex
        });
    }

    previous() {

        if (this.animating) return;

        const nextIndex = this.state.activeIndex === 0
            ?
            this.props.carouselItems.length - 1
            :
            this.state.activeIndex - 1;

        this.setState({
            activeIndex: nextIndex
        });
    }

    goToIndex(newIndex: number) {
        if (this.animating) return;
        this.setState({
            activeIndex: newIndex
        });
    }

    render() {

        const {
            activeIndex
        }: InstagramCarouselState = this.state;

        const {
            carouselItems: items,
            altText
        }: InstagramCarouselProps = this.props;

        const slides = items.map((item) => {
            return (
                <CarouselItem
                    onExiting={this.onExiting}
                    onExited={this.onExited}
                    key={item.images.standard_resolution.url}
                >
                    <Image
                        alt={altText}
                        src={item.images.standard_resolution.url}
                        fluid
                    />
                </CarouselItem>
            );
        });

        return (

            <Carousel
                activeIndex={activeIndex}
                next={this.next}
                previous={this.previous}
            >
                <CarouselIndicators
                    items={items}
                    activeIndex={activeIndex}
                    onClickHandler={this.goToIndex}
                />
                {slides}
                <CarouselControl
                    direction="prev"
                    directionText="Previous"
                    onClickHandler={this.previous}
                />
                <CarouselControl
                    direction="next"
                    directionText="Next"
                    onClickHandler={this.next}
                />
            </Carousel>
        );
    }
}